*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Plex", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  scrollbar-color: rgba(44, 57, 242, 0.5) rgba(44, 57, 242, 0.2);
  scrollbar-width: thin;
}
body::-webkit-scrollbar {
  width: 10px;
  height: 5px;
  border-radius: 10px;
}
body::-webkit-scrollbar-track-piece {
  background-color: rgba(44, 57, 242, 0.2);
  border-radius: 10px;
  width: 10px;
}
body::-webkit-scrollbar-thumb:vertical {
  height: 30px;
  background-color: rgba(44, 57, 242, 0.5);
  border-radius: 10px;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.vh-300 {
  min-height: 300vh;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}
@media (max-width: 767px) {
  .container--no-padding {
    padding: 0;
  }
}

.btn {
  border: none;
  background-color: #2c39f2;
  color: #ffffff;
  cursor: pointer;
  display: inline-flex;
  font-size: 16px;
  line-height: 1;
  font-weight: 600;
  margin: 0;
  padding: 17px 35px;
  transition: background-color 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn--form {
  border-radius: 8px;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  justify-content: center;
  margin: 34px 0 58px;
  max-width: 300px;
  padding: 10px 30px;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .btn--form {
    margin: 18px 0 58px;
  }
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .btn:hover, .btn:focus {
    background-color: #d62155;
  }
}

.header {
  background-color: #ffffff;
  border: 2px solid #f4f4ff;
  padding: 20px 0;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 30;
}
.header .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  margin: -8px 40px 0 0;
  display: flex;
}
@media (max-width: 1279px) {
  .header__logo {
    flex-direction: column;
    transform: scale(0.85);
  }
}
@media (max-width: 1023px) {
  .header__logo {
    flex-direction: row;
    transform: scale(1);
  }
}
.header__logo.is-active {
  position: relative;
  z-index: 25;
}
@media (max-width: 1279px) {
  .header__logo .logo__second-part {
    margin: 0;
  }
}
@media (max-width: 1023px) {
  .header__logo .logo__second-part {
    margin: 0 0 0 9px;
  }
}
@media (max-width: 767px) {
  .header__logo .logo__second-part {
    margin: 0;
  }
}
.header__git-link {
  display: flex;
  margin: 0 0 0 auto;
}
@media (max-width: 1023px) {
  .header__git-link {
    margin: 0 24px 0 auto;
  }
}

.footer {
  border-top: 2px solid #f4f4ff;
  padding: 22px 0;
}
.footer__wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
  align-items: center;
}
@media (max-width: 767px) {
  .footer__wrapper {
    gap: 25px;
  }
}
.footer__address {
  justify-self: start;
  font-style: normal;
  font-weight: 600;
  font-size: 12px;
  line-height: 150%;
}
@media (max-width: 767px) {
  .footer__address {
    font-size: 8px;
  }
}
.footer__logo {
  justify-self: center;
}
@media (max-width: 767px) {
  .footer__logo .logo__first-part {
    height: 13px;
    width: 58px;
  }
  .footer__logo .logo__second-part {
    height: 20px;
    margin: 0;
    width: 84px;
  }
}
.footer__git-link {
  justify-self: end;
}

.content {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: auto;
}
.content__wrapper {
  display: flex;
  flex: 1 0 auto;
  position: relative;
  margin: 0 auto;
  max-width: 1440px;
  width: 100%;
}
@media (max-width: 1023px) {
  .content__wrapper {
    padding: 0 30px;
  }
}
@media (max-width: 767px) {
  .content__wrapper {
    padding: 0 20px;
  }
}
.content__inner {
  border-left: 2px solid #f4f4ff;
  margin: 0 0 0 287px;
  padding: 0 60px 88px 70px;
  position: relative;
  width: 100%;
}
@media (max-width: 1023px) {
  .content__inner {
    border: none;
    margin: 0;
    padding: 0 0 70px;
  }
}
@media (max-width: 767px) {
  .content__inner {
    padding: 0 0 50px;
  }
}

.logo {
  align-items: baseline;
  display: flex;
}
@media (max-width: 767px) {
  .logo {
    flex-direction: column;
  }
}
.logo.is-active .logo__text {
  fill: #ffffff;
}
.logo.is-active .logo__symbol {
  fill: #2c39f2;
}
.logo.is-active .logo__shield {
  fill: #ffffff;
  transition: fill 0.3s ease;
}
.logo__first-part {
  height: 20px;
  position: relative;
  top: 1px;
  width: 89px;
}
.logo__second-part {
  height: 26px;
  margin: 0 0 0 9.5px;
  width: 134px;
}
.logo__text {
  fill: #000000;
  transition: fill 0.3s ease;
}
.logo__symbol {
  fill: #ffffff;
  transition: fill 0.3s ease;
}
.logo__shield {
  fill: #2c39f2;
  transition: fill 0.3s ease;
}

.git-link {
  display: flex;
  position: relative;
  z-index: 25;
}
.git-link.is-active svg {
  fill: #ffffff;
}
@media (hover: hover), screen and (min-width: 0\0 ) {
  .git-link:hover svg, .git-link:focus svg {
    fill: #2c39f2;
  }
}
.git-link svg {
  fill: #000000;
  height: 35px;
  transition: fill 0.3s ease;
  width: 35px;
}
@media (max-width: 1023px) {
  .git-link svg {
    height: 30px;
    width: 30px;
  }
}

.navbar {
  flex: 0 0 auto;
  height: 100%;
  left: 0;
  top: 0;
  width: 287px;
  background-color: #ffffff;
  position: absolute;
  z-index: 20;
}
@media (max-width: 1023px) {
  .navbar {
    background-color: #ffffff;
    bottom: 0;
    display: flex;
    flex-direction: column;
    left: 0;
    opacity: 0;
    margin: 0;
    padding: 68px 0 40px;
    position: fixed;
    pointer-events: none;
    top: 0;
    transition: all 0.3s ease;
    right: 0;
    visibility: hidden;
    width: 100%;
    z-index: 30;
  }
}
.navbar.is-active {
  opacity: 1;
  pointer-events: all;
  visibility: visible;
}
.navbar__close {
  background-color: rgba(255, 255, 255, 0);
  border: none;
  color: #000000;
  display: none;
  height: 20px;
  padding: 0;
  position: fixed;
  right: 20px;
  top: 30px;
  width: 20px;
}
.navbar__close::before {
  content: "";
  height: 40px;
  left: -10px;
  position: absolute;
  top: -10px;
  width: 40px;
}
@media (max-width: 1023px) {
  .navbar__close {
    display: flex;
  }
}
.navbar__close svg {
  stroke: currentColor;
}
.navbar__element:not(:last-child) {
  margin: 0 0 30px 0;
}
.navbar__element.is-active .navbar__button-icon {
  transform: rotate(180deg);
}
.navbar__wrapper {
  height: 100%;
  overflow-y: auto;
